html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--FONT-INTER);
  background-color: var(--BG-COLOR);
  color: var(--TEXT1-COLOR);
}
body .light-mode {
  background-color: white;
  color: black;
}

h1, h2, h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

a, a:visited, a:active {
  text-decoration: none;
}

:root {
  /* PRIMARY */
  --LIMEGREEN: hsl(163, 72%, 41%);
  --BRIGHTRED: hsl(356, 69%, 56%);
  --FACEBOOK: hsl(208, 92%, 53%);
  --TWITTER: hsl(0, 0%, 0%);
  --INSTAGRAM-START: hsl(37, 97%, 70%);
  --INSTAGRAM-END: hsl(329, 70%, 58%);
  --YOUTUBE: hsl(348, 97%, 39%);
  --BG-COLOR: var(--LIGHT-BG);
  --CARD-BG: var(--LIGHT-CARD-BG);
  --TEXT1-COLOR: var(--LIGHT-TEXT2);
  --TEXT2-COLOR: var(--LIGHT-TEXT1);
  --HOVER-COLOR: var(--LIGHT-CARD-HOVER);
  --TOGGLE-BUTTON: var(--LIGHT-BG);
  --TOGGLE-BG: var(--TOGGLE);
  --TOGGLE-LABEL: var(--LIGHT-TEXT1);
  /* TOGGLE */
  --TOGGLE-BG-START: hsl(210, 78%, 56%);
  --TOGGLE-BG-END: hsl(146, 68%, 55%);
  --TOGGLE: hsl(230, 22%, 74%);
  /* DARK THEME */
  --DARK-BG: hsl(230, 17%, 14%);
  --DARK-TOP-BG: hsl(232, 19%, 15%);
  --DARK-CARD-BG: hsl(228, 28%, 20%);
  --DARK-TEXT1: hsl(228, 34%, 66%);
  --DARK-TEXT2: hsl(0, 0%, 100%);
  --DARK-CARD-HOVER: hsl(228, 25%, 30%);
  /* LIGHT THEME */
  --LIGHT-BG: hsl(0, 0%, 100%);
  --LIGHT-TOP-BG: hsl(225, 100%, 98%);
  --LIGHT-CARD-BG: hsl(227, 47%, 96%);
  --LIGHT-TEXT1: hsl(228, 12%, 44%);
  --LIGHT-TEXT2: hsl(230, 17%, 14%);
  --LIGHT-CARD-HOVER: hsl(228, 33%, 91%);
}

@media (prefers-color-scheme: dark) {
  :root {
    --BG-COLOR: var(--DARK-BG);
    --CARD-BG: var(--DARK-CARD-BG);
    --TEXT1-COLOR: var(--DARK-TEXT2);
    --TEXT2-COLOR: var(--DARK-TEXT1);
    --HOVER-COLOR: var(--DARK-CARD-HOVER);
    --TOGGLE-BUTTON: var(--DARK-BG);
    --TOGGLE-BG: linear-gradient(225deg, var(--TOGGLE-BG-END) 0%, var(--TOGGLE-BG-START) 98.82%);
    --TOGGLE-LABEL: var(--DARK-TEXT1);
  }
}
body.light {
  --BG-COLOR: var(--LIGHT-BG);
  --CARD-BG: var(--LIGHT-CARD-BG);
  --TEXT1-COLOR: var(--LIGHT-TEXT2);
  --TEXT2-COLOR: var(--LIGHT-TEXT1);
  --HOVER-COLOR: var(--LIGHT-CARD-HOVER);
  --TOGGLE-BUTTON: var(--LIGHT-BG);
  --TOGGLE-BG: var(--TOGGLE);
  --TOGGLE-LABEL: var(--LIGHT-TEXT1);
}

body.dark {
  --BG-COLOR: var(--DARK-BG);
  --CARD-BG: var(--DARK-CARD-BG);
  --TEXT1-COLOR: var(--DARK-TEXT2);
  --TEXT2-COLOR: var(--DARK-TEXT1);
  --HOVER-COLOR: var(--DARK-CARD-HOVER);
  --TOGGLE-BUTTON: var(--DARK-BG);
  --TOGGLE-BG: linear-gradient(225deg, var(--TOGGLE-BG-END) 0%, var(--TOGGLE-BG-START) 98.82%);
  --TOGGLE-LABEL: var(--DARK-TEXT1);
}

:root {
  --FONT-INTER: "Inter", sans-serif;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.header {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2.25rem;
}
@media (min-width: 40em) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  width: 100%;
}
@media (min-width: 40em) {
  .header__title {
    width: auto;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--TEXT2-COLOR);
}
.header__toggle {
  margin-left: auto;
}

.toggle {
  display: grid;
  grid-template-columns: 1fr 3rem;
  border: none;
  margin: 0;
}
.toggle label {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--TOGGLE-LABEL);
}
.toggle label[for=dark] {
  line-height: 1.5rem;
  margin-right: 0.8125rem;
}
.toggle__wrapper {
  position: relative;
  height: 1.5rem;
}
.toggle input[type=radio] {
  margin: 0 -0.125rem 0 -0.125rem;
  opacity: 0;
  width: 1.5rem;
  height: 1.5rem;
}
.toggle input[type=radio]:focus ~ .toggle__button {
  border: 2px solid white;
}
.toggle__background {
  display: block;
  height: 100%;
  position: absolute;
  width: 100%;
  top: 0;
  border-radius: 0.75rem;
  background: var(--TOGGLE-BG);
  pointer-events: none;
}
.toggle__button {
  position: absolute;
  height: 1.125rem;
  width: 1.125rem;
  top: 0.1875rem;
  left: 0.1875rem;
  right: 100%;
  border-radius: 50%;
  background-color: var(--TOGGLE-BUTTON);
  transition: all 200ms ease-in-out;
  z-index: 2;
}
.toggle #dark:checked ~ .toggle__button {
  left: calc(100% - 21px);
  right: 3px;
}

.cards {
  display: grid;
  gap: 1.875rem;
  grid-template-columns: 1fr;
}
@media (min-width: 40em) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  background: var(--CARD-BG);
  color: var(--TEXT2-COLOR);
  text-align: center;
  padding: 1.5625rem;
  border-radius: 0.3125rem;
  position: relative; /* Ensure proper positioning of pseudo-element */
  overflow: hidden;
  transition: background 200ms ease-in-out;
  cursor: pointer;
}
.card:hover {
  background-color: var(--HOVER-COLOR);
}
.card--facebook {
  border-top: 4px solid var(--FACEBOOK);
}
.card--twitter {
  border-top: 4px solid var(--TWITTER);
}
.card--instagram {
  padding-top: 1.875rem;
}
.card--instagram::before {
  content: "";
  position: absolute;
  top: 0; /* Move pseudo-element up by the border width */
  left: 0;
  width: 100%;
  height: 0.25rem; /* Height should match the border width */
  background-image: linear-gradient(to right, var(--INSTAGRAM-START), var(--INSTAGRAM-END));
}
.card--youtube {
  border-top: 4px solid var(--YOUTUBE);
}
.card__platform {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1.75rem;
  margin-top: 0.3125rem;
  height: 1.25rem;
}
.card__subtitle {
  font-size: 0.875rem;
  font-weight: bold;
}
.card__icon {
  margin-right: 0.5rem;
}
.card__username {
  font-size: 0.75rem;
  font-weight: bold;
}
.card__followers {
  margin-bottom: 1.5625rem;
}
.card__count {
  color: var(--TEXT1-COLOR);
  letter-spacing: -0.125rem;
  margin-bottom: 0.25rem;
}
.card__count--big {
  font-size: 3.5rem;
  font-weight: bold;
}
.card__count--small {
  font-size: 2rem;
  font-weight: bold;
}
.card__label {
  font-size: 0.75rem;
  font-weight: 400;
  color: var(--DARK-TEXT1);
  text-transform: uppercase;
  letter-spacing: 0.3125rem;
  line-height: 0.0625rem;
}
.card__change {
  display: flex;
  font-size: 0.75rem;
  font-weight: bold;
  align-items: center;
  justify-content: center;
}
.card__change--up {
  color: var(--LIMEGREEN);
}
.card__change--down {
  color: var(--BRIGHTRED);
}
.card__change img {
  margin-right: 0.25rem;
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auot);
  justify-items: start;
  gap: 1.4375rem;
}
.card-grid .card__count {
  margin-bottom: 0;
}
.card-grid .card__count,
.card-grid .card__change {
  align-self: end;
}
.card-grid img, .card-grid .card__change {
  justify-self: end;
}